@import url(https://fonts.googleapis.com/css?family=Merriweather);
$red: #e74c3c;

*,
*:before,
*:after {
  box-sizing: border-box;
}

html, body {
  background: #f1f1f1;
  font-family: 'Merriweather', sans-serif;
}

article {
  display: flex;
  justify-content: center;
  //align-items: center;
  // height: 100vh; /* Fill the full viewport height */
  // margin: 0; /* Reset default margin */
  // padding: 0; /* Reset default padding */
  // background: #f0f0f0; /* Set a background color */
  // font-family: 'Merriweather', sans-serif;
}

h1 {
  text-align: center;
  color: #a8a8a8;
  text-shadow: 1px 1px 0 rgba(white, 1);
}

h2 {
  text-align: center;
  color: #a8a8a8;
  text-shadow: 1px 1px 0 rgba(white, 1);
}

form {
  max-width: 600px;
  text-align: center;
  margin: 20px auto;
  
  input, textarea {
    border:0; outline:0;
    padding: 1em;
    border-radius: 8px;
    display: block;
    width: 100%;
    margin-top: 1em;
    font-family: 'Merriweather', sans-serif;
    box-shadow: 0 1px 1px rgba(black, 0.1);
    resize: none;
    
    &:focus {
      box-shadow: 0 0px 2px rgba($red, 1) !important;
    }
  }
  
  #input-submit {
    color: white; 
    background: $red;
    cursor: pointer;
    
    &:hover {
      box-shadow: 0 1px 1px 1px rgba(#aaa, 0.6); 
    }
  }
  
  textarea {
      height: 126px;
  }
}

.half {
  float: left;
  width: 48%;
  margin-bottom: 1em;
}

.right { width: 50%; }

.left {
  margin-right: 2%; 
}

@media (max-width: 480px) {
  .half {
    width: 100%; 
    float: none;
    margin-bottom: 0; 
  }
}

.cf:before,
.cf:after {
    content: " "; 
    display: table; 
}

.cf:after {
    clear: both;
}

a {
  text-decoration: none;
}

header {
  text-align: center;
  padding: 20px 0;
  margin-top: 40px;
}

#logo {
  max-width: 129px;  /* Adjust as needed */
  height: auto;
  border-radius: 50%;
}

#logo-header {
  max-width: 40px;  /* Adjust as needed */
  height: auto;
  border-radius: 50%;
}

.white-link {
  color: white !important;
  /* Add any other styles you want to apply to the links on the home page */
}

.nav-link {
  font-family: 'Montserrat', sans-serif;
}

.navbar-brand {
  font-family: 'Montserrat', sans-serif;
}

.my-main-content {
  width: 90%; /* Adjust this if needed */
  max-width: 600px; /* Adjust this if needed */
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Aligns the logo to one side on larger screens */
}


.my-audio-card {
  width: 90%; /* Take up 90% of the viewport width. Adjust this as needed. */
  max-width: 600px; /* Set a maximum width for large screens */
  border: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  margin: 20px;
  margin-top: 150px;
  padding: 20px;
  box-sizing: border-box;
  background-color: rgba(240, 185, 213, 0.096);
}

.my-card-content {
  text-align: center;
}

.my-card-title {
  font-size: 20px;
  margin-bottom: 10px;
  color: black;
}

.my-card-description {
  font-size: 16px;
  margin-bottom: 20px;
}

audio {
  width: 100%;
}

#mylogo {
  max-width: 60px;  /* Adjust as needed */
  height: auto;
  border-radius: 50%;
}

@media screen and (max-width: 600px) {
  header {
      justify-content: center; /* Centers the logo on small screens */
  }

  .my-audio-card {
      margin-top: 20px; /* Add some space between the logo and audio card on small screens */
  }
}
